body {
    margin : 0;
    padding : 0;
    box-sizing: border-box;
}
:root {
    --resto-1-blue : #1D2B58;
}
#resto-header {
    height: 95vh;
    background-image: url(./img/resto-img-logo.avif);
    background-repeat: no-repeat;
    background-position: center;
    /* border: 1px solid green;    */
}
.navbar {
    background-color: var(--resto-1-blue);
    border: 1px solid black;
}
.resto-intro {
    top: 420px;
    position: relative;
    text-align: center;
    margin: 0;
    padding: 0;
}



.resto-intro h1{
    font-family: "kaushan script";
    font-size: 50px;
    margin: 0;
    padding: 0;
    color: var(--resto-1-blue);
}
.resto-intro p{
    font-family: 'Crimson Pro';
    font-size: 20px;
    margin: 0;
    padding: 0;
    color:var(--resto-1-blue);
}
.resto-intro .fleche {
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}
#resto-menu{
    border: 1px solid red ;
    min-height: 300px;
    display: flex;
}
.menu {
    border: 1px solid black  ;
    display: grid;
    width: 100%;
    margin :5px ;
    padding: 5px;
}
.menu .row  {
    box-sizing: border-box;
    border: 1px yellow solid;
    padding: 5px;
    margin: 5px;
}
.menu .col  {
    border: 1px blue solid;
    margin: 5px;
    padding: 5px;
}

.menu h5 {
    text-align: center;
}